import { useState } from "react";
import {useHistory} from "react-router-dom";

const AddBlog = () => {
    const [title, setTitle] = useState("");
    const [body, setBody] = useState("");
    const [author, setAuthor] = useState("Hassaan");

    const [ispending, setIspending] = useState(false);
    const history = useHistory();

    const addHandle = (e) => {
       
        e.preventDefault();
        const blog = {title, body, author};
        setIspending(true);

     fetch("http://localhost:8000/blogs", {
      method: "Post",
      headers: {"Content-Type": "application/json"},
      body: JSON.stringify(blog)
     }).then((res) => {
         setIspending(false);
         history.push("/");
     });
     
    }
    return ( 
       <div className="AddBlog">
           <div className="container mt-5">
               <div className="row justify-content-center">
                   <div className="col-md-5">
                   <form onSubmit={addHandle}>
               <div className="form-group">
                   <label className="text-left">Title:</label>
                   <input type="text" className="form-control"
                   required
                   value={title}
                   onChange={(e) => setTitle(e.target.value)}
                   />
                   </div>
                   <div className="form-group">
                   <label>Body:</label>
                   <textarea className="form-control" cols="5" rows="5"
                     required
                     value={body}
                     onChange={(e) => setBody(e.target.value)}></textarea>        
               </div>
               <div className="form-group">
                   <label>Author:</label>
                   <select
                   className="form-control"
                   required
                   value={author}
                   onChange={(e) => setAuthor(e.target.value)}
                   >
                       <option value="Hassaan">Hassaan</option>
                       <option value="Qasim">Qasim</option>
                       <option value="Hashim">Hashim</option>
                       <option value="James">James</option>
                       <option value="Janes">Janes</option>
                   </select>
               </div>
               {!ispending && <button className="btn btn-success btn-block mt-4">Add Post</button>}
               {ispending && <button className="btn btn-success btn-block mt-4">Post Adding...</button>}
           </form>
                   </div>
               </div>
        
           </div>
       </div>
     );
}
 
export default AddBlog;