import {Link} from "react-router-dom";

const BlogList = ({data, heading}) => {
      
    return ( 
        <div className="BlogList ">
          <h1 className="mt-5 mb-4">{heading}</h1>
  {data.map((cur) => (
<div className="inside"  key={cur.id}>
  <Link  to={`/BlogDetail/${cur.id}`}> 
<h2>{cur.title}</h2>
<p>Written by {cur.author}</p>
</Link>
</div>
)
)}
        </div>
     );
}
 
export default BlogList;