import {useParams, useHistory} from "react-router-dom";
import useFetch from "./useFetch";

const BlogDetail = () => {
    const {id} = useParams();

    const history = useHistory();

    const {data, isPending, error} = useFetch(`http://localhost:8000/blogs/${id}`);

    const deleteHnadle = (ID) => {
     fetch(`http://localhost:8000/blogs/${ID}`, {
         method: "Delete",
     }).then(() => {
      history.go(-1);
     });
    }

    return ( 
        <div className="BlogDetail">
            <div className="container text-center">
                <div className="row justify-content-center">
        {isPending && <p>Loading...</p>}
      {error && <p>{error}</p>}
      {data && (
          <div className="inside2">
              <h2 className="pt-4">{data.title}</h2>
              <div className="pt-2 pb-3">Written by {data.author}</div>
              <p className="text-justify">{data.body}</p>
              <button className="btn btn-danger py-1" onClick={() => {deleteHnadle(data.id)}}>Delete</button>
          </div>
      )}            
                </div>
            </div>
        </div>
     );
}
 
export default BlogDetail;